.css-19r6kue-MuiContainer-root .css-oe53i6-MuiGrid-root {
  height: 300px;
  overflow-y: scroll;
}

.donorBg {
  background-color: #f9f9f9;
  min-height: 100vh;
  width: 100%;
}

.createHeader {
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
  /* or 125% */

  display: flex;
  align-items: center;

  color: #212529;
}

.subCreate {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  /* or 129% */

  display: flex;
  align-items: center;

  /* Text Color/Dark */

  color: #343a40;
}
.ovalImage {
  height: 120px;
  width: 120px;
  border-radius: 60px;
  background-color: #ced4da;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.addimageicon {
  height: 40px;
  width: 40px;
}
.filesizelabel {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height, or 129% */

  display: flex;
  align-items: center;

  /* Text Color/Medium */

  color: #6c757d;
}
.progressOfform {
  background-color: white;
}

.tabsformVertical {
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  /* or 120% */

  display: flex;
  align-items: center;

  /* Text Color/Dark */

  color: #343a40;
}

form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

.menu-item {
  padding: 0 40px;
  margin: 5px 10px;
  user-select: none;
  cursor: pointer;
  border: 1px transparent solid;
}
.menu-item-wrapper {
  border: 1px transparent solid;
}
.menu-item-wrapper.active {
  border: 1px blue solid;
}
.menu-item.active {
  border: 1px green solid;
}

.scrollbar-hidden::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge add Firefox */
.scrollbar-hidden {
  -ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
}
.cardIcons {
  height: 20px;
  width: 20px;
}
.main {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #676767;
}

.container {
  position: fixed;
  z-index: 1000;
  width: 0 auto;
  bottom: 30px;
  margin: 0 auto;
  left: 30px;
  right: 30px;
  display: flex;
  flex-direction: "column";
  pointer-events: none;
  align-items: flex-end;

  @media (max-width: 680px) {
    align-items: center;
  }
}

.message {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  width: 40ch;

  @media (max-width: 680px) {
    width: 100%;
  }
}

.content {
  color: white;
  background: #445159;
  opacity: 0.9;
  padding: 12px 22px;
  font-size: 1em;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px;
  overflow: hidden;
  height: auto;
  border-radius: 3px;
  margin-top: 10px;
  margin-bottom: 0;
}

.button {
  cursor: pointer;
  pointer-events: all;
  outline: 0;
  border: none;
  background: transparent;
  display: flex;
  align-self: flex-end;
  overflow: hidden;
  margin: 0;
  padding: 0;
  padding-bottom: 14px;
  color: rgba(255, 255, 255, 0.5);
}

.button:hover {
  color: rgba(255, 255, 255, 0.6);
}

.life {
  position: absolute;
  bottom: 0;
  left: 0px;
  width: auto;
  background-image: linear-gradient(130deg, #00b4e6, #00f0e0);
  height: 5px;
}

